import PressPopupPlus from '../press-popup-plus/press-popup-plus.vue';
import ToolBar from '../press-picker-plus/toolbar.vue';
import PressButton from '../press-button/press-button.vue';


import { VueCropper } from '../common/vue3/vue-cropper';






import { CROPPER_PROPS } from './props';


export default {
  name: 'PressCropper',
  options: {
    styleIsolation: 'shared',
  },
  components: {
    PressPopupPlus,
    PressButton,
    ToolBar,


    VueCropper,




  },
  props: {
    ...CROPPER_PROPS,
    show: {
      type: Boolean,
      default: false,
    },
    showTitle: {
      type: Boolean,
      default: true,
    },
    title: { type: String, default: '裁剪图片' },
    cancelButtonText: {
      type: String,
      default: '',
    },
    confirmButtonText: {
      type: String,
      default: '',
    },
    toolbarClass: {
      type: String,
      default: '',
    },
    isESport: {
      type: Boolean,
      default: false,
    },
    h5Option: {
      type: Object,
      default: () => ({}),
    },
    delayShow: {
      type: Number,
      default: 200,
    },
  },
  data() {
    return {
      showCropper: false,
      timer: null,

      lastCropped: {},
    };
  },
  watch: {
    show: {
      handler(value) {
        if (value) {
          clearTimeout(this.timer);
          this.timer = setTimeout(() => {
            this.showCropper = true;
          }, this.delayShow);
        } else {
          clearTimeout(this.timer);
          this.timer = setTimeout(() => {
            this.showCropper = false;
          }, 300);
        }
      },
      immediate: true,
    },
  },
  mounted() {
  },
  methods: {
    afterDraw(...args) {
      this.$emit('afterDraw', ...args);
    },
    beforeDraw(...args) {
      this.$emit('beforeDraw', ...args);
    },
    cropped(imagePath, imageInfo) {
      this.$emit('cropped', imagePath, imageInfo);

      this.lastCropped = {
        imagePath,
        imageInfo,
      };
    },
    onClose() {
      this.$emit('close');
    },
    confirm() {

      this.$refs.cropper.getCropData((data) => {
        this.$emit('confirm', data);
      });





    },
    cancel() {
      this.$emit('cancel');
    },
  },
};

